<template>
  <div class="page">
    <div class="logo">
      <h1>亿发<span>商城</span></h1>
      <div></div>
    </div>
    <div class="form">
      <van-field
        v-model="numbering"
        placeholder="请选择商户编号"
        @focus="onNumbering"
      >
        <img
          class="icon"
          width="21px"
          height="17px"
          slot="left-icon"
          src="../assets/images/loginRegistered/numbering.png"
          alt=""
        >
      </van-field>
      <van-field
        v-model="username"
        placeholder="请输入手机号"
      >
        <img
          class="icon"
          width="21px"
          height="17px"
          slot="left-icon"
          src="../assets/images/loginRegistered/username.png"
          alt=""
        >
      </van-field>
      <van-field
        v-model="password"
        placeholder="请输入密码"
        :type="showPassword?typeText:typePassword"
      >
        <img
          class="icon"
          width="21px"
          height="17px"
          slot="left-icon"
          src="../assets/images/loginRegistered/password.png"
          alt=""
        >
        <img
          class="icon"
          width="21px"
          height="17px"
          slot="right-icon"
          alt=""
          :src="showPassword?showPass.passwordShow:showPass.passwordHide"
          @click="onShow"
        >
      </van-field>
      <div class="funcGroup">
        <van-button
          round
          type="info"
          size="large"
          class="login"
        >登录</van-button>
        <router-link :to="{name:'registered'}">
          <van-button
            round
            type="default"
            size="large"
            class="register"
          >立即注册</van-button>
        </router-link>
      </div>
      <div class="other">
        <router-link :to="{name:'retrievepassword'}">
          <div>忘记密码？</div>
        </router-link>
        <router-link :to="{name:'verificationLogin'}">
          <div>手机验证码登录</div>
        </router-link>
      </div>
      <van-popup
        v-model="numberingShow"
        position="bottom"
        :overlay="true"
      >
        <van-picker
          show-toolbar
          :columns="columns"
          @cancel="onCancel"
          @confirm="onConfirm"
        />
      </van-popup>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      numbering:'', //商户编号
      numberingShow: false,
      columns: ['杭州', '宁波', '温州', '嘉兴', '湖州'],
      username: '', //用户名
      password: '', //密码
      typePassword: 'password',
      typeText: 'text',
      showPassword: false,
      showPass: {
        passwordShow: '/static/image/show.png',
        passwordHide: '/static/image/hide.png'
      },
    };
  },
  created() {
    
      axios.post('/api', {
        'action':'sedMobileMessage',
        'phone': '18274862815'
      }).then(res => {
        console.log(res)
      }, err => {
        console.log(err)
      })
  },
  methods: {
    onNumbering() {
      this.numberingShow = true
    },
    onConfirm(value, index) {
      this.numberingShow = false
      this.numbering = value
      console.log(`当前值：${value}, 当前索引：${index}`);
    },
    onCancel() {
      this.numberingShow = false
      console.log('取消');
    },
    // 切换眼睛
    onShow() {
      this.showPassword = !this.showPassword;
    },
 
  }
};
</script>

<style lang="less" scoped>
.page {
  .logo {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2.4rem;
    color: #0055ff;
    font-size: 18px;
    line-height: 0.96rem;
    h1 {
      span {
        color: #000000;
        font-weight: 300;
      }
    }
    div {
      width: 1.07rem;
      height: 0.11rem;
      margin: 0.81rem 0 1.02rem;
      background-color: #0055ff;
      border-radius: 2px;
    }
  }
  .form {
    padding: 0 1rem;
    .icon {
      vertical-align: middle;
    }
    .van-field {
      margin-top: 0.67rem;
      border-bottom: solid 1px #a0a0a0;
      .van-field__left-icon {
        margin-right: 0.35rem;
      }
    }
    .funcGroup {
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      flex-direction: column;
      align-items: center;
      .login {
        width: 7.99rem;
        height: 0.93rem;
        margin-top: 0.76rem;
        background-color: #0055ff;
        box-shadow: 0 0.03rem 0.21rem 0 rgba(0, 85, 255, 0.41);
        border-radius: 18px;
        font-size: 15px;
        line-height: 0.93rem;
      }
      .register {
        width: 7.99rem;
        height: 0.93rem;
        margin-top: 0.28rem;
        border-radius: 18px;
        border: solid 1px #bfbfbf;
        color: #666666;
        font-size: 15px;
        line-height: 0.93rem;
      }
    }
    .other {
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-top: 0.48rem;
      a {
        color: #000000;
        &:active {
          color: #000000;
        }
      }
    }
    .numbering {
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
    }
  }
}
</style>


